export type RestOfDays = number | null

//       
export function calcRestOfDaysBeforeDeadline(deadline: string): RestOfDays {
  if (deadline === '') {
    return null
  }

  const [day, month, year] = deadline.split('.')

  const deadlineDate: Date = new Date(`${month}/${day}/${year}`) //     Date  (05/11/2025)

  const currentDate: Date = new Date()

  const diffInMs = deadlineDate.getTime() - currentDate.getTime() //   

  //    
  return Math.floor(diffInMs / (1000 * 60 * 60 * 24)) + 1 // +1 -   
}
